import Modal from 'react-modal';
import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom'
// import AdvertPaymentModal from '../AdvertPaymentModal';
import AdvertisementApiList from '../../../AdvertisementApiList';
import ApiHeaderAdvrt from '../../../ApiHeaderAdvrt';
import ViewAppliedApplication from '../../../Advertisement/ViewAppliedApplication';
import BanquetMarriageApplicationFullDetails from './BanquetMarriageApplicationFullDetails';
import BanquetMarriagePaymentModal from './BanquetMarriagePaymentModal';
import BrandLoader from '../../../../../Components/Common/BrandLoader';
import CommonModal from '../../../../../Components/GlobalData/CommonModal';
import ServerErrorCard from '../../../ServerErrorCard';
import { nullToNA } from '../../../../../Components/Common/PowerUps/PowerupFunctions';
import ReuploadBanquetDocModal from './ReuploadBanquetDocModal';





const customStyles = {
    content: {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        marginRight: '-50%',
        transform: 'translate(-50%, -50%)',
        backgroundColor: 'white',
        border: 'none',
        overflowY: "scroll"
    },
};
Modal.setAppElement('#root');

function BanquetMarriagePendingApplicationList(props) {
    const navigate = useNavigate()


    const { api_getBanquetAppliedApplicationList, api_getBanquetApprovedApplicationList, api_getBanquetRejectedApplicationList, api_getBanquetApplicationDetailForPayment } = AdvertisementApiList()

    const [applyList, setapplyList] = useState('hidden')
    const [appliedApplication, setappliedApplication] = useState()
    const [approvedApplication, setapprovedApplication] = useState()
    const [rejectedApplication, setrejectedApplication] = useState()
    const [openPaymentModal, setOpenPaymentModal] = useState(0)
    const [applicationIdDetail, setapplicationIdDetail] = useState()
    const [applicationType, setapplicationType] = useState()
    const [applicationNo, setapplicationNo] = useState()
    const [isLoading, setisLoading] = useState(false);
    const [erroState, seterroState] = useState(false);

    const [tabIndex, settabIndex] = useState(1)
    const [modalIsOpen, setIsOpen] = useState(false);
    const [modalIsOpen1, setIsOpen1] = useState(false);
    const openModal = () => setIsOpen(true)
    const closeModal = () => setIsOpen(false)
    const afterOpenModal = () => { }
    const openModal1 = () => setIsOpen1(true)
    const closeModal1 = () => setIsOpen1(false)
    const afterOpenModal1 = () => { }


    const modalAction = (applicationId, applicationType) => {
        console.log("..............application id..............", applicationId)
        console.log("..............application type..............", applicationType)
        setapplicationNo(applicationId)
        setapplicationType(applicationType)
        openModal()
    }
    console.log("application no. for modal", applicationNo)


    const docUploadModal = (applicationId) => {
        console.log("..............application id..............", applicationId)
        setapplicationNo(applicationId)
        openModal1()
    }


    const showApplyList = () => {
        applyList == 'hidden' ? setapplyList('') : setapplyList('hidden')
    }

    ///////////{*** GET APPLICATION LIST***}/////////
    useEffect(() => {
        getApplicationList()
    }, [])
    const getApplicationList = () => {
        seterroState(false)
        setisLoading(true)
        const requestBody = {
            deviceId: "selfAdvert",
        }
        axios.post(`${api_getBanquetAppliedApplicationList}`, requestBody, ApiHeaderAdvrt())
            .then(function (response) {
                console.log('applied application in Banquet/Marriage Hall', response.data.data)
                setappliedApplication(response.data.data)
                setisLoading(false)
            })
            .catch(function (error) {
                console.log('errorrr.... ', error);
                seterroState(true)
                setisLoading(false)
            })
    }

    ///////////{*** APPROVED APPLICATION LIST***}/////////
    useEffect(() => {
        getApprovedApplicationList()
    }, [])
    const getApprovedApplicationList = () => {
        const requestBody = {
            // deviceId: "selfAdvert",
        }
        axios.post(`${api_getBanquetApprovedApplicationList}`, requestBody, ApiHeaderAdvrt())
            .then(function (response) {
                console.log('banquet approved 12', response.data.data)
                setapprovedApplication(response.data.data)
            })
            .catch(function (error) {
                console.log('errorrr.... ', error);
            })
    }

    console.log("approved application list...", approvedApplication)

    ///////////{*** REJECTED APPLICATION LIST***}/////////
    useEffect(() => {
        getRejectedApplicationList()
    }, [])
    const getRejectedApplicationList = () => {
        const requestBody = {
            // deviceId: "selfAdvert",
        }
        axios.post(`${api_getBanquetRejectedApplicationList}`, requestBody, ApiHeaderAdvrt())
            .then(function (response) {
                console.log('rejected application in Banquet/Marriage Hall', response.data.data)
                setrejectedApplication(response.data.data)
            })
            .catch(function (error) {
                console.log('errorrr.... ', error);
            })
    }
    console.log("rejected application list...", rejectedApplication)


    const handlePayment = (e) => {
        console.log("application id", e.target.id)
        let applicationId = e.target.id
        const requestBody = {
            applicationId: applicationId
        }
        axios.post(`${api_getBanquetApplicationDetailForPayment}`, requestBody, ApiHeaderAdvrt())
            .then(function (response) {
                console.log('application detail for payment 1', response.data.data)
                setapplicationIdDetail(response.data.data)
                setOpenPaymentModal(prev => prev + 1)
            })
            .catch(function (error) {
                console.log('errorrr.... ', error);
            })
    }

    if (isLoading) {
        return (
            <>
                <BrandLoader />
                <div className='min-h-screen'></div>
            </>
        )
    }
    if (erroState) {
        return (
            <CommonModal>
                <ServerErrorCard title="Server is busy" desc="Server is too busy to respond. Please try again later." buttonText="View Dashboard" buttonUrl="/propertyDashboard" />
            </CommonModal>
        )
    }

    console.log("application details for payment 2", applicationIdDetail)
    console.log("rejected application list...", rejectedApplication)
    console.log("approved application list...", approvedApplication)
    console.log("application  list...1", appliedApplication?.data)
    console.log("application  no.", applicationNo)

    return (
        <>
            {/***** section 3 ******/}
            <div className=''>
                <BanquetMarriagePaymentModal openPaymentModal={openPaymentModal} applicationDetails={applicationIdDetail} showLoader={props.showLoader} />
            </div>

            <div className=''>
                <div className='grid grid-cols-1 md:grid-cols-12 lg:grid-cols-12 bg-slate-50 mb-4 shadow-md p-4 border border-indigo-500 '>
                    <div className='col-span-12 text-2xl mb-2 font-normal text-gray-800 dark:text-gray-300'>
                        {tabIndex == 1 && <h1 className='flex-1 '>Banquet / Marriage<span className='text-indigo-500 underline'>Pending Application</span> </h1>}
                        {tabIndex == 2 && <h1 className='flex-1 '>Banquet / Marriage<span className='text-indigo-500 underline'>Approved Application</span></h1>}
                        {tabIndex == 3 && <h1 className='flex-1 '>Banquet / Marriage<span className='text-indigo-500 underline'>Rejected Application</span></h1>}
                    </div>
                    <div className='col-span-12 text-left'>
                        <button type='button' className='text-xs md:text-sm lg:text-sm bg-indigo-300 px-1 py-0 mr-2 text-indigo-600 border border-indigo-500  rounded leading-5  focus:bg-indigo-500  focus:shadow-lg focus:text-white darks:bg-white' onClick={() => settabIndex(1)}>Pending Application</button>
                        <button type='button' className='text-xs md:text-sm lg:text-sm bg-indigo-300 px-1 py-0 mr-2 text-indigo-600 border border-indigo-500  rounded leading-5  focus:bg-indigo-500  focus:shadow-lg focus:text-white darks:bg-white' onClick={() => settabIndex(2)}>Approved Application</button>
                        <button type='button' className='text-xs md:text-sm lg:text-sm bg-indigo-300 px-1 py-0 mr-2 text-indigo-600 border border-indigo-500  rounded leading-5  focus:bg-indigo-500  focus:shadow-lg focus:text-white darks:bg-white' onClick={() => settabIndex(3)}>Rejected Application</button>
                    </div>
                </div>

                {/* pending application list for Banquet/Marriage Hall */}
                {tabIndex == 1 &&
                    <div>
                        {appliedApplication === undefined || appliedApplication === null || appliedApplication?.data?.length == 0 ?
                            <>
                                <h1 className='text-center text-2xl text-gray-500'>No Pending Application Found ... </h1>
                                <img src='https://cdn-icons-png.flaticon.com/512/7466/7466140.png' className='mx-auto h-36 mt-4' />

                            </>
                            :
                            <div className=''>
                                {appliedApplication?.data?.map((items) => (
                                    <div className='col-span-3 p-3 h-auto mb-4 shadow-lg rounded leading-5 bg-white transform transition duration-300 ease-in-out hover:-translate-y-2 hover:shadow-xl  '>

                                        <div className='grid grid-cols-1 md:grid-cols-1 lg:grid-cols-1 bg-yellow-500  w-36 -ml-3 rounded-r-lg shadow-md top-0 absolute '>
                                            <h1 className='text-xs  text-center text-white '>Banquet/Marriage Hall</h1>
                                        </div>
                                        <div className='grid grid-cols-1 md:grid-cols-3 lg:grid-cols-3 p-2'>
                                            <h1 className='text-xs'>Application No. :- <span className='font-bold text-sm text-gray-700'>{nullToNA(items?.application_no)}</span></h1>
                                            <h1 className='text-xs'>Applicant Name :- <span className='font-bold text-sm text-gray-700'>{nullToNA(items?.applicant)}</span></h1>
                                            <h1 className='text-xs'>Apply Date :- <span className='font-bold text-sm text-gray-700'>{nullToNA(items?.application_date)}</span></h1>
                                        </div>
                                        <div className='grid grid-cols-1 md:grid-cols-3 lg:grid-cols-3  p-2'>
                                            <h1 className='text-xs'>Entity Name :- <span className='font-bold text-sm text-gray-700'>{nullToNA(items?.entity_name)}</span></h1>
                                            <h1 className='text-xs'>Entity Address :- <span className='font-bold text-sm text-gray-700'>{nullToNA(items?.entity_address)
                                            }</span></h1>
                                        </div>

                                        <div className='grid grid-cols-1 md:grid-cols-1 lg:grid-cols-1 p-2'>
                                            <div className='justify-end'>
                                                {items?.doc_upload_status == 0 &&
                                                    <button type="button" id={items?.id} value='Active' class="float-right text-xs   shadow-lg mr-2 rounded leading-5 border-gray-50 bg-indigo-500 px-2 py-1 text-white" onClick={(e) => docUploadModal(items?.id)}>upload Document</button>
                                                }
                                                <button type="button" id={items?.id} value='Active' class="float-right text-xs   shadow-lg mr-2 rounded leading-5 border-gray-50 bg-indigo-500 px-2 py-1 text-white" onClick={(e) => modalAction(items?.id, e.target.value)}>View</button>
                                            </div>
                                        </div>
                                    </div>
                                ))}
                            </div>
                        }
                    </div>
                }

                {/* approved application list for Banquet/Marriage Hall */}
                {tabIndex == 2 &&
                    <div>
                        {approvedApplication === undefined || approvedApplication === null || approvedApplication?.data?.length == 0 ?
                            <>
                                <h1 className='text-center text-2xl text-gray-500'>No Approved Application Found ... </h1>
                                <img src='https://cdn-icons-png.flaticon.com/512/7466/7466140.png' className='mx-auto h-36 mt-4' />

                            </>
                            :
                            <div className=''>
                                {approvedApplication?.data?.map((items) => (
                                    <div className='col-span-3 p-3 h-auto mb-4 shadow-lg rounded leading-5 bg-white transform transition duration-300 ease-in-out hover:-translate-y-2 hover:shadow-xl  '>
                                        <div className='grid grid-cols-1 md:grid-cols-1 lg:grid-cols-1 bg-yellow-500  w-36 -ml-3 rounded-r-lg shadow-md top-0 absolute '>
                                            <h1 className='text-xs  text-center text-white '>Banquet/Marriage Hall</h1>
                                        </div>
                                        <div className='grid grid-cols-1 md:grid-cols-3 lg:grid-cols-3 p-2'>
                                            <h1 className='text-xs'>Application No. :- <span className='font-bold text-sm text-gray-700'>{nullToNA(items?.application_no)}</span></h1>
                                            <h1 className='text-xs'>Applicant Name :- <span className='font-bold text-sm text-gray-700'>{nullToNA(items?.applicant)}</span></h1>
                                            <h1 className='text-xs'>Apply Date :- <span className='font-bold text-sm text-gray-700'>{nullToNA(items?.application_date)}</span></h1>
                                        </div>
                                        <div className='grid grid-cols-1 md:grid-cols-3 lg:grid-cols-3 p-2'>
                                            <h1 className='text-xs'>Entity Name :- <span className='font-bold text-sm text-gray-700'>{nullToNA(items?.entity_name)}</span></h1>
                                            <h1 className='text-xs'>Entity Address :- <span className='font-bold text-sm text-gray-700'>{nullToNA(items?.entity_address)
                                            }</span></h1>
                                            <h1 className='text-xs'>License No. :- <span className='font-bold text-sm text-gray-700'>{nullToNA(items?.license_no)
                                            }</span></h1>

                                        </div>

                                        <div className='grid grid-cols-1 md:grid-cols-1 lg:grid-cols-1  p-2'>
                                            <div className='flex-1 justify-end'>
                                                {items?.renew_option == 1 &&
                                                    <button type="button" id={items?.id} value='Approve' class="float-right text-xs   shadow-lg mr-2 rounded leading-5 border-gray-50 bg-indigo-500 px-2 py-1 text-white" onClick={() => navigate(`/marriage-hall/${items?.id}/renew`)}>
                                                        Renew License
                                                    </button>
                                                }
                                                <button type="button" id={items?.id} value='Approve' class="float-right text-xs   shadow-lg mr-2 rounded leading-5 border-gray-50 bg-indigo-500 px-2 py-1 text-white" onClick={(e) => modalAction(items?.id, e.target.value)}>
                                                    View
                                                </button>
                                                {items.payment_status == 1 ?
                                                    <h1 className='text-xs font-BreeSerif text-green-500'><span><button className='float-right  shadow-lg mr-2 rounded leading-5 border-gray-50 bg-indigo-500 px-2 py-1 text-white' onClick={() => navigate('/approvalLetter')}>Download Approval Letter</button></span></h1>
                                                    :
                                                    <button type="button" id={items?.id} value={items?.payment_amount} class="float-right text-xs   shadow-lg mr-2 rounded leading-5 border-gray-50 bg-green-500 px-2 py-1 text-white" onClick={handlePayment}>Pay</button>
                                                }
                                            </div>
                                        </div>
                                    </div>
                                ))}
                            </div>
                        }
                    </div>
                }


                {/* rejected  application list for Banquet/Marriage Hall */}
                {tabIndex == 3 &&
                    <div>
                        {rejectedApplication === undefined || rejectedApplication === null || rejectedApplication?.data?.length === 0 ?
                            <>
                                <h1 className='text-center text-2xl text-gray-500'>No Rejected Application Found ... </h1>
                                <img src='https://cdn-icons-png.flaticon.com/512/7466/7466140.png' className='mx-auto h-36 mt-4' />

                            </>
                            :
                            <div className=''>
                                {rejectedApplication?.data?.map((items) => (
                                    <div className='col-span-3 p-3 h-auto mb-4 shadow-lg rounded leading-5 bg-white transform transition duration-300 ease-in-out hover:-translate-y-2 hover:shadow-xl  '>
                                        <div className='grid grid-cols-1 md:grid-cols-1 lg:grid-cols-1 bg-yellow-500  w-36 -ml-3 rounded-r-lg shadow-md top-0 absolute '>
                                            <h1 className='text-xs  text-center text-white '>Banquet/Marriage Hall</h1>
                                        </div>
                                        <div className='grid grid-cols-1 md:grid-cols-3 lg:grid-cols-3 p-2'>
                                            <h1 className='text-xs'>Application No. :- <span className='font-bold text-sm text-gray-700'>{nullToNA(items?.application_no)}</span></h1>
                                            <h1 className='text-xs'>Applicant Name :- <span className='font-bold text-sm text-gray-700'>{nullToNA(items?.applicant)}</span></h1>
                                            <h1 className='text-xs'>Apply Date :- <span className='font-bold text-sm text-gray-700'>{nullToNA(items?.application_date)}</span></h1>
                                        </div>
                                        <div className='grid grid-cols-1 md:grid-cols-3 lg:grid-cols-3 p-2'>
                                            <h1 className='text-xs'>Entity Name :- <span className='font-bold text-sm text-gray-700'>{nullToNA(items?.entity_name)}</span></h1>
                                            <h1 className='text-xs'>Entity Address :- <span className='font-bold text-sm text-gray-700'>{nullToNA(items?.entity_address)
                                            }</span></h1>

                                        </div>

                                        <div className='grid grid-cols-1 md:grid-cols-1 lg:grid-cols-1 p-2'>
                                            <div className='flex-1 justify-end'>
                                                <button type="button" id={items?.temp_id
                                                } value='Reject' class="float-right text-xs   shadow-lg mr-2 rounded leading-5 border-gray-50 bg-indigo-500 px-2 py-1 text-white" onClick={(e) => modalAction(items?.id, e.target.value)}>View</button>
                                            </div>
                                        </div>
                                    </div>
                                ))}
                            </div>
                        }
                    </div>
                }
            </div>
            <Modal
                isOpen={modalIsOpen}
                onAfterOpen={afterOpenModal}
                onRequestClose={closeModal}
                style={customStyles}
                contentLabel="Example Modal"
            >
                <div class="mx-auto px-0 overflow-y-auto" style={{ 'width': '80vw', 'height': '100%' }}>
                    <div className='h-[44rem] bg-white overflow-y-auto '>
                        <BanquetMarriageApplicationFullDetails data={applicationNo} applicationType={applicationType} showLoader={props.showLoader} closeModal={closeModal} />
                    </div>
                </div>
            </Modal>

            {/*if document is BTC then reupload document */}
            <Modal
                isOpen={modalIsOpen1}
                onAfterOpen={afterOpenModal1}
                onRequestClose={closeModal1}
                style={customStyles}
                contentLabel="Example Modal"
            >
                <div class="mx-auto px-0 overflow-y-auto" style={{ 'width': '50vw', 'height': '100%' }}>
                    <div className='h-[44rem] bg-violet-50 overflow-y-auto '>
                        <ReuploadBanquetDocModal data={applicationNo} closeModal={closeModal1} />
                    </div>
                </div>
            </Modal>


        </>
    )
}

export default BanquetMarriagePendingApplicationList